<template>
  <div class="user-page">
    <div class="userInfo">
      <img :src="user.avatar" alt="">
      <h3>{{ user.username }}</h3>
    </div>
    <van-grid clickable :column-num="3" :border="false">
  <van-grid-item icon="clock-o" text="历史记录" to="/"/>
  <van-grid-item icon="bookmark-o" text="我的收藏" to="/collect"/>
  <van-grid-item icon="thumb-circle-o" text="我的点赞" to="/like"/>

</van-grid>
<van-cell-group>
  <van-cell title="推荐分享" is-link />
  <van-cell title="意见反馈" is-link />
  <van-cell title="关于我们" is-link />
  <van-cell title="推出登录" is-link @click="logout"/>
</van-cell-group>
  </div>
</template>

<script>
import { delToken } from '@/utils/user'
import { getUserInfo } from '@/api/user'
export default {
  name: 'UserPage',
  async created () {
    const res = await getUserInfo()
    this.user = res.data.data
  },
  data () {
    return {
      user: ''
    }
  },
  methods: {
    logout () {
      delToken()
      this.$toast.success('退出登录')
      this.$router.push('/login')
    }
  }
}
</script>

<style scoped lang="less">
div.user-page{
  background-color: #f5f5f5;
  height: 100vh;
  .userInfo{
    display: flex;
    align-items: center;
    padding: 20px;
    img{
      height: 80px;
      width: 80px;
      border-radius: 50%;
      overflow: hidden;
    }
    h3{
      margin-left: 20px;
    }
  }
  .van-grid{
    margin-bottom: 20px;
  }
}
</style>
